import { Badge } from "@/components/ui/badge";
import { Card, CardContent } from "@/components/ui/card";
import { Minus, Plus, Trash2 } from "lucide-react";
import Image from "next/image";
import { productsData } from "../constants/constants";

interface CartItemProps {
  id: number;
  quantity: number;
}

export function CartItem({ id, quantity }: CartItemProps) {
  const product = productsData.find(p => p.id === id) || {
    id: 0,
    name: "Producto no encontrado",
    image: "/placeholder-product.jpg",
    sku: "N/A",
    category: "",
  };

  const handleAdd = () => {
    console.log("Añadir 1 al producto:", id);
  };

  const handleRemove = () => {
    console.log("Quitar 1 al producto:", id);
  };

  const handleDelete = () => {
    console.log("Eliminar producto:", id);
  };

  return (
    <Card className="w-full">
      <CardContent className="p-4">
        <div className="flex items-center gap-4">
          <div className="relative h-16 w-16 flex-shrink-0">
            <Image
              src={product.image}
              alt={product.name}
              fill
              className="rounded-md object-cover"
            />
          </div>

          <div className="flex-1 min-w-0">
            <h3 className="font-medium text-sm break-words">
              {product.name}
            </h3>
            <p className="text-muted-foreground text-xs">
              SKU: {product.sku}
            </p>
          </div>

          <div className="flex flex-col items-center gap-2">
            <Plus
              className="text-emerald-600 h-4 w-4 hover:scale-[1.1] cursor-pointer transition-all"
              onClick={handleAdd}
            />

            <div className="bg-emerald-500 text-white w-8 flex justify-center items-center">
              {quantity}
            </div>
            {/* <Badge
              variant="secondary"
              className="min-w-8 justify-center text-white bg-emerald-400">
              {quantity}
            </Badge> */}

            <Minus
              className="h-4 w-4 text-red-500 hover:scale-[1.1] cursor-pointer transition-all"
              onClick={handleRemove}
              style={{ opacity: quantity <= 1 ? 0.5 : 1 }}
            />
          </div>

          <div
            className="py-8 bg-red-500 hover:bg-red-600 cursor-pointer transition-colors w-8 flex justify-center "
            onClick={handleDelete}>
            <Trash2 className="h-4 w-4 text-white" />
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
